@import url(https://fonts.googleapis.com/css?family=Lato:400,700,700i,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
    margin: 0px;
    padding: 0px;
    font-family: 'Lato', sans-serif;
    scroll-behavior: smooth;
}
img{
    max-width: 100%;
}
h1,h2,h3,h4,h5,h6,p{
    margin:0px;
    padding: 0px;
}

.header{
    width:100%;
    height:80px;
    box-sizing:border-box;
    background-color: blueviolet;
    margin: 0px;
    padding: 0px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    width: 100px;
    font-size: 30px;
    color: #ffff;
    cursor: pointer;
}
.header ul{
    margin: 0px;
    padding: 0px;
    list-style: none;
}
.header ul li {
    display: inline-block;
    float: left;
}
.header ul li a{
    text-decoration: none;
    font-size: 14px;
    color: #fff;
    padding: 10px 12px;
}

.card_row{
    display: flex;
    width: 80%;
    margin: 20px auto;
    flex-direction: row;
    flex-wrap: wrap;

}
.user_card{
    width: 150px;
    height: 150px;
    border: 1px solid #ccc;
    background-color: #ccc;
    border-radius: 3px;
    margin-right: 15px;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 10px;
    box-sizing: border-box;
}
.user_card h3{
    margin: 5px 0px;

}

.btn{
    height: 30px;
    padding: 5px;
    text-align: center;
    margin: 10px auto;
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn a{
    color: #fff;
    text-decoration: none;
    font-weight: 600;
    display: block;
}
.btn_primary{
    color: #fff;
    background-color: blueviolet;
    width: 100px;
    border-radius: 3px;
}
.container{
    width:90%;
    margin: auto;
    overflow: hidden;
}
.input_search_main{
    width: 100%;
    margin: 10px auto;
}
.input_search{
    width: 100%;
    height: 40px;
    padding: 8px;
    box-sizing: border-box;
    font-size: 15px;
    margin-bottom: 10px;
}
.search_btn{
    width: 100%;
    height: 40px;
    margin: auto;
    text-align: center;
    background-color: blueviolet;
    color: #fff;
    border: 0px;
    border-radius: 3px;
    font-size: 16px;
    font-weight: 600;

}
.error{
    background-color: #d8ecec;
    color: #cc0a0a;
    padding: 4px;
    font-size: 14px;
    font-weight: 200;
}
.modal{
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 1;
    background: rgba(27, 26, 26, 0.5);
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
    margin: auto;

}
.modal_content{
    width: 500px;
    height: 500px;
    background-color: #fff;
    box-sizing: border-box;
    border-radius: 3px;
}


/* [class^="col-"]
{
background: red;
font-size: 50px;
} */

[class^="col_"] {
    background-color: red;
}
.alert-light{
    background-color: lightblue;
    padding: 10px;
}
.alert-primery{
    background-color: rgb(0, 133, 177);
    padding: 10px;
}
.single_user_card{
    width: 400px;
    border: 1px solid #ccc;
    padding: 10px;
    margin: 20px auto;

}
.single_user_card img{
    margin: auto;
    text-align: center;
    display: block;
    margin-bottom: 10px;
}
.single_user_card  h1{
    margin: 10px;
}
.single_user_card p{
    border: 1px solid #ccc;
    padding: 5px;
    white-space: normal;
    margin-bottom: 10px;
}
.green{
    color: green;
}
.red{
    color: red;
}
.repos_card{
    width: 100%;
    border: 1px solid #ccc;
    padding: 10px;
    box-sizing: border-box;
    margin: 10px auto;
    background: #d8ecec;
}
.repos_card a{
    color: red;
}
.clr_btn{
    width: 100%;
    background: darkred;
    color: #fff;
    border: 0px;
    height: 40px;
    font-size: 15px;
    border-radius: 3px;
}


.login{
    width: 400px;
    border:1px solid #ccc;
    padding: 20px;
    margin: 20px auto;
}


.login_title{
    margin:10px;
    text-align: center;
    text-transform: uppercase;
}
.form-control{
    width: 100%;
    height: 38px;
    box-sizing: border-box;
    margin: 10px 0px; 
    padding: 10px;
    border-radius: 3px;
    border: 1px solid #8e6be2;
    font-size: 17px;
    font-weight: bold;
}
.form-control:focus{
    outline: none;
    box-shadow: 1px 4px 2px #8E6BE3;
}
.submit_btn{
    width: 130px;
    font-size: 21px;
    font-weight: 500;
    background: #8e6be2;
    height: 40px;
    color: #fff;
    border: 4px solid rgb(215, 212, 219);
    border-radius: 3px;
    cursor: pointer;
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
    outline: none;
}
.submit_btn:hover{
    background: transparent;
    color: #000;
}
